
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    title: {
      type: String,
      default: ''
    },
    listData: {
      type: Array,
      required: true
    },
    listCount: {
      type: Number,
      default: 0
    },
    propList: {
      type: Array,
      required: true
    },
    //接收是否显示列的序号
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    //接收是否显示选中框
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    //分页功能:双向绑定的属性
    page: {
      type: Object,
      //默认值
      default: () => ({ currentPage: 0, pageSize: 10 })
    },
    //接收展开值
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    //是否有分页
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emits: ['selectionChange', 'update:page'],
  setup(props, { emit }) {
    const handleSelectionChange = (value: any) => {
      //可以拿到选中的一行的数据
      // console.log(value)
      //将拿到的数据,通过事件发出
      emit('selectionChange', value)
    }
    const handleSizeChange = (pageSize: number) => {
      emit('update:page', { ...props.page, pageSize })
    }
    const handleCurrentChange = (currentPage: number) => {
      emit('update:page', { ...props.page, currentPage })
    }

    return {
      handleSelectionChange,
      handleSizeChange,
      handleCurrentChange
    }
  }
})
